import http from "../http";


export { getVoiceDiscernService }

//获取用户邀请列表
const getVoiceDiscernService = (params) => {
    return new Promise((resolve, reject) => {
        // let reqUrl = 'http://10.18.222.105:6310/assistant/intentionparse'
        // let reqUrl = 'http://10.18.222.105:6603/cloudbrain-assistant/assistant/intentionparse'
        
        let reqUrl = 'cloudbrain-service-gateway/assistant/intentionparse';
        http.get(reqUrl , params, 'baseUrl').then(result => {
            result.resultCode == '0' ? resolve(result.data) : reject(result.msg);
        }).catch(error => {
            reject(error);
        })
    });
}